<template>
  <div>
    <div>
      <div class="wyl_retrieve">
        <van-field v-model="sms" center clearable placeholder="请输入手机号">
          <template #button>
            <a
              :class="flag ? 'registerBtngray' : 'registerBtn'"
              @click="registerLogin"
              >{{ btnText }}</a
            >
          </template>
        </van-field>
        <van-cell-group>
          <van-field v-model="sss" placeholder="请输入验证码" />
        </van-cell-group>
        <van-cell-group>
          <van-field v-model="password" placeholder="请输入密码" />
        </van-cell-group>
      </div>
      <div class="wyl_login_button">
        <button @click="onClickRegister">确定</button>
      </div>
    </div>
  </div>
</template>

<script>
import Vue from 'vue';
import { Toast } from 'vant';

Vue.use(Toast);
export default {
  data() {
    return {
      sms: "",
      sss: "",
      password: "",
      btnText: "发送验证码",
      flag: false,
    };
  },
  methods: {
    //获取验证码
    registerLogin() {
      if (/^1[34578]\d{9}$/.test(this.sms)) {
        this.$APP
          .smsCode({
            mobile: this.sms,
            sms_type: "login",
          })
          .then((res) => {
            if (res.data.code != 200) {
              console.log(res);
              Toast(res.data.msg);
            } else {
              this.countDown();
            }
          });
      } else {
        Toast("请输入正确的手机号");
        return;
      }
    },
    //获取验证码倒计时
    countDown() {
      let num = 60;
      setInterval(() => {
        num--;
        this.btnText = "获取验证码(" + num + ")";
        this.flag = true;
        if (num <= 0) {
          this.btnText = "获取验证码";
          this.flag = false;
        }
      }, 1000);
    },
    //登录
    onClickRegister() {
      this.$APP
        .change({
          mobile: this.sms,
          password: this.password,
          sms_code: this.sss,
        })
        .then((res) => {
          let token = res.data.data.remember_token;
          window.localStorage.setItem("token", token);
          this.$router.push("/user");
        });
    },
  },
};
</script>

<style>
.wyl_retrieve {
  width: 100%;
  height: 25vh;
}
.wyl_login_button {
  width: 100%;
  height: 10vh;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-size: 0.8rem;
}
.wyl_login_button button {
  width: 90%;
  height: 7vh;
  background: orange;
  border-radius: 18px;
  border: none;
}
.registerBtn {
  color: #da5c2a;
}
.registerBtngray {
  color: gray;
}
</style>